import React from "react";
import { useSpring, animated } from "react-spring";
import Check from "../assets/check.png";

function Thankyou() {
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { delay: 1000, duration: 500 },
  });
  return (
    <animated.div style={props}>
      <div className="thankyou-container">
        <img src={Check} className="check-image" alt="check" />
        <p className="thankyou-text">Thank you</p>
        <p className="feedback-text">
          Thank you for your response. We as a technology firm are committed to
          work in the education space in providing digital solutions enabling
          teachers and school administrators to profoundly improve the quality
          of education being imparted. As our first endeavour, we have
          prioritized to work on challenges with regards to managing Student’s
          School Assignments for both teachers as well as parents. As we embark
          in this journey, we would love to have you by our side all along
          helping us with your valuable suggestions and feedback.
          <br />
          <br />
        </p>
        <p className="feedback-progress-text">
          We will keep you posted on our progress.
        </p>
      </div>
    </animated.div>
  );
}

export default Thankyou;
